<template>

  <li class="shop-item">
    <router-link :to="href">
    <div class="mix-box">
      <img class="mix-pic" v-lazy="image" />
      <div class="mix-info">
        <h3 class="mix-title">{{ title }} <span class="orange">{{ salary }}</span></h3>
        <p class="mix-desc">{{ source }}</p>
      </div> 
    </div>

    <ul class="item-foot">
      <li>
        <i class="common-icon icon-loc-gray"></i>
        <span>{{location}}</span>
      </li>
      <li>
        <i class="common-icon icon-gift-gray"></i>
        <span v-for="(item, index) in welfare.split(',')">{{item}}</span>
      </li>
    </ul>
    </router-link>
  </li>

</template>

<script>

  export default {
    name: 'shop-item',

    props: {
      image: {
        type: String,
        default: ''
      },
      title: {
        type: String,
        default: ''
      },
      salary: {
        type: String,
        default: ''
      },
      source: {
        type: String,
        default: ''
      },
      location: {
        type: String,
        default: ''
      },
      welfare: {
        type: String,
        default:''
      },
      href: {
        type: String,
        default:''
      }
    }

  };
</script>

<style lang="css">

  .mix-box{
    display: flex;
  }
  .mix-box .mix-pic{
    width:60px;
    height:60px;
    border-radius: 30px;
  }
  .mix-box .mix-info{
    flex: 1;
    margin-left:10px;
    color: #1E1E1E;
  }
  .mix-box .mix-title{
    margin:10px 0;
    font-size: 17px;
    line-height: 20px;
  }
  .mix-box .mix-desc{
    margin:0;
    font-size: 15px;
    line-height: 16px;
  }

  .shop-item{
    list-style:none;
    margin:10px 0;
    padding:20px 15px;
    background: #FFFFFF;
    border-top: 1px solid #E9E9E9;
    border-bottom: 1px solid #E9E9E9;
  }

  .shop-item .item-foot{
    margin:20px 0 0 0;
    padding: 15px 0 0 0;
    border-top: 1px dashed #E9E9E9;
    list-style:none;
    display:flex;
  }

  .shop-item .item-foot li{
    margin-right:20px;
    font-size:15px;
    color:#828282;
  }
  .shop-item .item-foot li span{
    margin:0 3px;
    vertical-align: middle;
  }

  
</style>



